html {
  font-size: 62.5%;
  margin: 0;
  padding: 0; }

body {
  background-color: #fff;
  font-size: 1.4rem;
  color: #fff;
  font-family: "Roboto";
  overflow-x: hidden; }

a {
  color: #000; }

p {
  margin: 0; }

.header {
  background: url("/img/bg-header.jpg") top no-repeat; }

.top-header {
  background-color: #009eb2;
  height: 105px; }
  .top-header__content {
    display: flex;
    align-items: center;
    height: 9rem; }
  .top-header__column {
    flex: 0 1 50%; }
    .top-header__column--logo {
      align-self: flex-start;
      padding-top: 2.5rem;
      flex: 0 0 19rem;
      text-align: center; }

.menu__list {
  display: grid;
  grid-template-columns: 11rem 13rem 18rem; }
  .menu__list li {
    margin: 0.1rem 0; }

.menu__link {
  color: #fff;
  font-family: "PLay";
  height: 2.7rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0.3rem 2.2rem;
  text-decoration: underline;
  transition: 0.3s ease; }
  @media (min-width: 720px) {
    .menu__link:hover {
      border-radius: 20px;
      background-color: #068292; } }

.contacts-header {
  display: flex;
  justify-content: center; }
  .contacts-header__column {
    border-right: 1px solid #fff;
    padding: 0 3rem; }
    .contacts-header__column:last-child {
      border: 0; }
  .contacts-header__item--icon {
    background: url("../img/phone.png") left no-repeat;
    padding-left: 3.7rem; }
  .contacts-header__label {
    font-size: 1.2rem;
    font-weight: 300;
    color: #fff;
    margin-bottom: 0.5rem; }
  .contacts-header__phone {
    font-size: 1.8rem;
    font-weight: 900;
    color: #fff; }

.bottom-header__container {
  height: 6.3rem;
  display: flex;
  align-items: center; }

.bottom-header__column {
  flex: 0 1 50%; }

.actions-header {
  display: flex; }
  .actions-header__login {
    padding-left: 3.6rem;
    background: url("../img/login.png") left no-repeat;
    display: inline-block;
    line-height: 2.4rem;
    color: #999; }
    .actions-header__login span {
      border-bottom: 1px dotted; }
  .actions-header__signup {
    padding-left: 3.6rem;
    background: url("../img/user.png") left no-repeat;
    display: inline-block;
    line-height: 2.4rem;
    color: #1ab9ce; }
    .actions-header__signup span {
      border-bottom: 1px dotted; }

.info-header__schedule {
  color: #000; }
